 import React, { Component, useState, useEffect, useRef } from 'react';
 import './App.css';
//  import disney from './aesset/disney_YouTube.mp4';


function Video ( props ){
    let video_jin = useRef();
    
    useEffect( () => { 
        video_jin.current.classList.toggle('main_video2'); 
      //  console.log('l');
        const colors = ["#478ae2", "#a341ca"];
        const numBalls = 25;
        const balls = [];

        for (let i = 0; i < numBalls; i++) {
          let ball = document.createElement("div");
          ball.classList.add("ball");
          ball.style.background = colors[Math.floor(Math.random() * colors.length)];
          ball.style.left = `${Math.floor(Math.random() * 100)}vw`;
          ball.style.top = `${Math.floor(Math.random() * 100)}vh`;
          ball.style.transform = `scale(${Math.random()})`;
          ball.style.width = `${Math.random()}em`;
          ball.style.height = ball.style.width;
      
          balls.push(ball);
          video_jin.current.append(ball);
        }

        // Keyframes
        balls.forEach((el, i, ra) => {
                let to = {
                            x: Math.random() * (i % 2 === 0 ? -11 : 11),
                            y: Math.random() * 12
                        };

                let anim = el.animate(
                  [{ transform: "translate(0, 0)" },
                  { transform: `translate(${to.x}rem, ${to.y}rem)` }],
                    {
                      duration: (Math.random() + 1) * 2000, // random duration
                      direction: "alternate",
                      fill: "both",
                      iterations: Infinity,
                      easing: "ease-in-out"
                    }
                );                  
           });      

        return () => { }
     }, [] );    

    return(
        <div id="main_video" ref={video_jin} onClick={ () => { props.setonvideo(false); } } >   
            {/* <video autoPlay loop={true} muted={true} controls >
              <source src={disney}></source> 
            </video>                  */}
            <p style={{ color : 'rgb(223, 213, 126)'} }>ì˜ìƒì´ ìˆì§€ë§Œ íŒŒì¼ í¬ê¸°ê°€ ì»¤ì„œ repositoryì— commitì´ ì•ˆë˜ì–´ ìƒëµí•©ë‹ˆë‹¤...</p>
        </div>
    );
}

export default Video;  